.white {
    background-color: #FFFFFF;
}

.black {
    color: black;
}

.line-chart-section {
  width: 260px;
  border-radius: 5px;
  background-color: #FF0000;
  grid-area: line-chart-section;
  height: 280px;
}


.title-line-chart {
    overflow-wrap: break-word;
    max-width: 9em;
    padding-left: 2em;
    padding-top: 1em;
    font-size: 1em;
    font-weight: 400;
    line-height: 24px;
    color: #FFFFFF;
    opacity: 0.6;
    position: absolute;
    overflow: hidden;
}