
.navbar-vertical p {
    color: #fff;
    font-size: 0.6em;
    font-weight: 500;
    -webkit-transform: rotate(-90deg);
    transform: rotate(-90deg);
    position: relative;
    top: -3em;
}

.navbar-vertical {
    align-items: center;
    background-color: #020203;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-around;
    left: 0;
    margin-right: 500px;
    overflow-x: hidden;
    position: fixed;
    top: 0;
    width: 117px;
    z-index: -1;
    box-shadow: 2px 2px 2px rgb(0, 0, 0);
}

.iconcontainer {
    align-items: center;
    background-color: #fff;
    border-radius: 6px;
    display: flex;
    height: 64px;
    justify-content: center;
    margin-top: 20px;
    width: 64px;
    box-shadow: 5px 5px 5px rgb(0, 0, 0);
}

.iconcontainer:hover {
    opacity: 0.4em;
    box-shadow: 4px 4px 8px rgb(88, 88, 88);
}

.icon-boxes {
    padding-top: 9em;
}


@media screen and (max-width: 1200px) {
    .navbar-vertical p {
        font-size: 0.5em;
    }
}